export interface Skill {
  id: string;
  name: string;
  description: string;
  type: 'active' | 'constitution'; // Active = Action, Constitution = Passive Body Trait
  cost?: number; // Point cost if applicable
  icon: string;
  effectDescription?: string; // Short text for UI
}

export interface Archetype {
  id: string;
  name: string;
  description: string;
  icon: string; 
  passiveSkill: Skill;
  // 
  permanentState: {
    name: string; // 
    bonus: PermanentBonus; // 
  };
  hidden?: boolean; // 
}

export interface CharacterData {
  name: string;
  age: number;
  gender: Gender;
  appearance: string;
  personality: string;
  background: string;
  difficulty: Difficulty;
  archetypeId: string | null;
  
  // The core stats -  MVU 
  attributes: CharacterAttributes;
  
  // Body stats
  height: number; 
  weight: number; 
  cupSize: string; 
  hips: number; 
  cockLength: number; 
  maleGenitalType?: string; // hasPenistrue
  femaleGenitalType?: string; // hasBreaststrue 

  // Body Configuration (For Non-binary mostly)
  configFeatures: {
    hasBreasts: boolean;
    hasPenis: boolean;
  };

  // Skills
  initialActiveSkills: string[]; // IDs of selected active skills
  initialPassiveSkills: string[]; // IDs of selected constitution skills
}

