// ====================  ====================

/**  */
export enum SkillType {
  /**  -  */
  PHYSICAL = 'physical',
  /**  -  */
  MENTAL = 'mental',
  /**  -  */
  CHARM = 'charm',
  /**  -  */
  LUCK = 'luck',
  /**  -  */
  SUPPORT = 'support',
  /**  -  */
  CONTROL = 'control',
  /**  -  */
  FIELD = 'field',
  /**  -  */
  ULTIMATE = 'ultimate',
}

/**  */
export enum DamageSource {
  /**  -  */
  SEX_POWER = 'sex_power',
  /**  -  */
  CHARM = 'charm',
  /**  -  */
  LUCK = 'luck',
  /**  -  */
  WILLPOWER = 'willpower',
  /**  -  */
  LEVEL = 'level',
  /**  -  */
  POTENTIAL = 'potential',
  /**  -  */
  FIXED = 'fixed',
  /**  -  */
  TARGET_LUST = 'target_lust',
  /**  -  */
  TARGET_MISSING_STAMINA = 'target_missing_stamina',
  /**  -  */
  SELF_LUST = 'self_lust',
}

/**  */
export enum SkillTarget {
  /**  */
  SINGLE_ENEMY = 'single_enemy',
  /**  */
  ALL_ENEMIES = 'all_enemies',
  /**  */
  SELF = 'self',
  /**  */
  SINGLE_ALLY = 'single_ally',
  /**  */
  ALL_ALLIES = 'all_allies',
  /**  -  */
  EVERYONE = 'everyone',
  /**  */
  RANDOM_ENEMY = 'random_enemy',
}

/** Buff/Debuff  */
export enum BuffType {
  // ===  ===
  /**  */
  ATK_UP = 'atk_up',
  /**  */
  DEF_UP = 'def_up',
  /**  */
  ENDURANCE_UP = 'endurance_up',
  /**  */
  CRIT_UP = 'crit_up',
  /**  */
  DODGE_UP = 'dodge_up',
  /**  -  */
  REGEN = 'regen',
  /**  -  */
  SHIELD = 'shield',
  /**  -  */
  REFLECT = 'reflect',
  /**  -  */
  LIFESTEAL = 'lifesteal',
  /**  -  */
  STEALTH = 'stealth',
  /**  -  */
  RAGE = 'rage',
  /**  -  */
  FOCUS = 'focus',
  
  // ===  ===
  /**  */
  ATK_DOWN = 'atk_down',
  /**  */
  DEF_DOWN = 'def_down',
  /**  */
  ENDURANCE_DOWN = 'endurance_down',
  /**  */
  CRIT_DOWN = 'crit_down',
  /**  */
  DODGE_DOWN = 'dodge_down',
  /**  -  */
  DOT_LUST = 'dot_lust',
  /**  */
  WILLPOWER_DOWN = 'willpower_down',
  /**  -  */
  SILENCE = 'silence',
  /**  -  */
  BIND = 'bind',
  /**  -  */
  CONFUSION = 'confusion',
  /**  -  */
  CHARM_DEBUFF = 'charm_debuff',
  /**  -  */
  FEAR = 'fear',
  /**  -  */
  AROUSAL = 'arousal',
  /**  -  */
  SENSITIVE = 'sensitive',
  /**  -  */
  SHAME = 'shame',
  /**  -  */
  EDGE = 'edge',
  /**  -  */
  POST_ORGASM = 'post_orgasm',
  /**  -  */
  EXHAUSTED = 'exhausted',
  /**  -  */
  HEAT = 'heat',
  /**  -  */
  MARKED = 'marked',
}

/**  */
export enum SkillRarity {
  /**  -  */
  COMMON = 'C',
  /**  -  */
  RARE = 'B',
  /**  -  */
  EPIC = 'A',
  /**  -  */
  LEGENDARY = 'S',
  /**  -  */
  MYTHIC = 'SS',
}

/**  */
export enum UnlockConditionType {
  /**  */
  LEVEL = 'level',
  /**  */
  CONSTITUTION = 'constitution',
  /**  */
  SKILL_PREREQUISITE = 'skill_prerequisite',
  /**  */
  ATTRIBUTE = 'attribute',
  /**  */
  AFFECTION = 'affection',
  /**  */
  EVENT = 'event',
  /**  */
  WINS = 'wins',
  /**  */
  ORGASM_COUNT = 'orgasm_count',
}

// ====================  ====================

/** Buff/Debuff  */
export interface BuffEffect {
  /** Buff  */
  type: BuffType;
  /**  */
  value: number;
  /**  */
  isPercent: boolean;
  /**  (-1 ) */
  duration: number;
  /**  */
  stackable: boolean;
  /**  */
  maxStacks?: number;
  /**  */
  trigger?: string;
}

/**  */
export interface DamageComponent {
  /**  */
  source: DamageSource;
  /**  */
  coefficient: number;
  /**  */
  baseValue: number;
}

/**  */
export interface UnlockCondition {
  /**  */
  type: UnlockConditionType;
  /**  */
  value: string | number;
  /**  */
  description: string;
}

/**  */
export interface SkillUpgrade {
  /**  */
  cost: number;
  /**  */
  damageIncrease: number;
  /**  */
  costReduction: number;
  /**  */
  cooldownReduction: number;
  /**  */
  specialEffect?: string;
}

/**  */
export interface SkillData {
  // ===  ===
  /** ID */
  id: string;
  /**  */
  name: string;
  /**  */
  description: string;
  /**  */
  effectDescription: string;
  /**  */
  icon: string;
  /**  */
  type: SkillType;
  /**  */
  rarity: SkillRarity;
  
  // ===  ===
  /**  */
  staminaCost: number;
  /**  */
  lustCost?: number;
  /**  */
  willpowerCost?: number;
  /**  */
  cooldown: number;
  /** 0=>0 */
  castTime: number;
  
  // ===  ===
  /**  */
  damageFormula: DamageComponent[];
  /**  */
  target: SkillTarget;
  /** 100 = 100% */
  accuracy: number;
  /**  */
  critModifier: number;
  /**  Buff/Debuff */
  buffs: BuffEffect[];
  
  // ===  ===
  /**  */
  ignoreDefense: boolean;
  /**  */
  canBeDodged: boolean;
  /**  */
  canBeReflected: boolean;
  /**  */
  hitCount: number;
  /**  */
  selfEffect?: BuffEffect[];
  /**  */
  conditionalEffects?: {
    condition: string;
    effect: BuffEffect;
  }[];
  
  // ===  ===
  /**  */
  unlockConditions: UnlockCondition[];
  /** 5 */
  upgrades: SkillUpgrade[];
  /**  */
  currentLevel?: number;
  
  // ===  ===
  /**  */
  animationType?: string;
  /** ID */
  soundEffect?: string;
  /**  */
  voiceLine?: string;
}

/**  */
export interface LearnedSkill {
  /** ID */
  skillId: string;
  /**  */
  level: number;
  /**  */
  currentCooldown: number;
  /**  */
  equipped: boolean;
  /** 0-5 */
  slotIndex?: number;
}

